.page {
    min-height: 90vh;
    width: 100%;
}
#home {
    text-align: left;
    min-height: 100vh;
}

#authPage  {
    width: 100%;
    display: grid;
    grid-auto-flow: column;
}

#viewToBuy {
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: 60%;
}
#viewToBuy .buySection {
    display: grid;
    grid-auto-flow: row;
    width: 100%;
    padding-bottom: 30%;
    background-color: white;
}
.buySection .buyDetails {
    width: 95%;
    margin-left: auto;
    margin-right: auto;
    top: 5%;
    display: grid;
    grid-auto-flow: row;
    grid-auto-rows: minmax(3em, max-content);
    background-color: white;
    box-shadow: 0px 12px 12px rgba(0, 0, 0, 0.034);
}
.buyDetails span {
    width: 100%;
    display: grid;
    grid-auto-flow: column;
}
.buyDetails span div {
    width: 100%;
    height: 3em;
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: 40% 60%;
}
.buyDetails span div p {
    min-width: min-content;
    width: 130px;
    text-overflow: ellipsis;
    height: calc(fit-content + 5px);
    text-indent: 5%;
    line-height:;
    text-align: left;
    background-color: #D1D1D1;
}
.buyDetails span div h2, .buyDetails span div em{
    text-align: left;
    text-indent: 5%;
    font-weight: lighter;
    width: 100%;
}



@media only screen and (max-width: 600px){
    .buySection {
        border-top-left-radius: 15px;
        border-top-right-radius: 15px;
    }
    .authPage {
        grid-auto-flow: row;
    }
    #viewToBuy {
        grid-auto-flow: row;
        grid-template-columns: none;
    }
    #viewToBuy span {
        grid-auto-flow: row;
        grid-auto-rows: none;
    }
    .buySection {
        box-shadow: 0px -4px 12px rgba(0, 0, 0, 0.322);
        height: 100vh;
    }

}
