
@media only screen and (max-width: 600px){
    p {
        font-weight: light;
    }
    nav {
      height: 4em !important;
      line-height: 4em !important;
    }
    .companyTitle {
      font-size: 8vw !important;
      height: 4vw !important;
    }
    .imageViewer {
      position: sticky !important;
      top: 0;
    }
    .mainImg {
      height: 40vh !important;
    }
    .hamburgher {
      display: grid;
    }
    .mainNav .linkBtns {
      display: none !important;
    }
    .homeTopNav {
      margin-top: 7%;
        height: 200px !important;
    }
    .homeTopNav div h1 {
        font-size: 200% !important;
    }
    .homeTopNav .banner img {
      width: 70%;
    }
    .homeTopNav .banner h1 {
      text-align: right;
    }
    .category {
      width: 95% !important;
    }
    .showProduct {
      width: 100% !important;
      grid-auto-flow: column !important;
      display: none;
      grid-template-columns: 40%;
    }
    .authForm {
      width: 90% !important;
      margin-left: auto;
      margin-right: auto;
    }
    .categoryIcons {
        transition: height 1s, width 1s !important;
    }
    .mobileSearchBar button {
        height: 3em;
        width: 8em;
        background-color: #FB244F;
        color: white;
    }
    
    .mobileSearchBar .faSearch{
        width: 1em;
        height: 1em;
        color: white;
    }
    .mobileSearchBar div {
        position: absolute;
        width: 100vw;
        display: grid;
        grid-auto-flow: column;
        background-color: white;
        margin-left: calc(100% - 65vw);
        padding-bottom: 1vh;
        box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.288);
    }
    /* foot navigation in android */
    .footNav {
        position: fixed !important;
        bottom: 0;
        height: 4em;
        line-height: 4em;
        width: 100%;
        background-color: white;
        display: grid;
        grid-auto-flow: column;
        z-index: 5000;
        box-shadow: 0px -4px 12px rgba(0, 0, 0, 0.267);
    }
    .addProductForm * {
        width: 100% !important;
    }
    .addProductForm {
        padding-top: 5%;
        padding-bottom: 70% !important;
    }

  }
  