
.subPage {
    height: 90vh;
}

/* see and add product sub page */
#addprod {
    width: 100%;
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: 30%;
}
#addprod section:nth-child(1){
    width: 90%;
    height: 100%;
    margin-left: auto;
    margin-right: auto;
    overflow-y: auto;
    background-color: #2020200e;
}
#addprod section:nth-child(1) .h1 {
    width: 100%;
    text-align: left !important;
    background-color: #F3F3F3;
    position: sticky;
    top: 0;
    margin-left: auto!important;
    z-index: 200;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.267);
}
/* category view */
#categoryView {
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: 20%;
}

#messages {
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: 30%;
}
#messages section:nth-child(1) {
    width: 100%;
    height: 100%;
    background-color: #2020200e;
}
#messages section:nth-child(1) h1 {
    background-color: #E8E8E8;
    width: 100%;
    position: sticky;
    top: 0;
    z-index: 200;
    margin-left: 0;
    box-shadow: -4px 4px 12px rgba(0, 0, 0, 0.199);
}

@media only screen and (max-width: 600px){
    #addprod {
        grid-auto-flow: row;
        grid-template-columns: none;
    }
    #addprod section:nth-child(1){
        padding-bottom: 25%;
        overflow: visible;
    }
    #categoryView {
        grid-auto-flow: row;
        grid-template-columns: none;
    }
}
