

/* imageViewer */
.imageViewer {
    width: 100%;
    height: 80vh;
    display: grid;
    grid-auto-flow: row;
    grid-template-rows: 85%;
}
.mainImg {
    width: 100%;
    height: 100%;
}
.arrowBtn {
    width: 3em;
    background-color: #F62147;
    color: white;
    height: 3em;
    top: calc((100% - 3em) / 2);
    border-radius: 50%;
    position: absolute;
    z-index: 100;
}

.mainImg img {
    width: 90%;
    height: 90%;
    margin-left: auto;
    margin-right: auto;
    object-fit: contain;
}
.imageViewer .selectImage {
    width: 100%;
    height: 20vh;
    display: grid;
    background-color: #bbbbbb;
    grid-auto-flow: column;
    justify-content: flex-start;
}
.selectImage div {
    width: 20vh;
    height: 20vh;
}
.selectImage div img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

/* convListItem styling */
.convListItem {
    width: 90%;
    margin-right: auto;
    margin-left: auto;
    text-align: left;
    padding: 2%;
    background-color: #F3F3F3;
    box-shadow: 0px 0px 12px rgba(0, 0, 0, 0.158);
}


/* filters styling */
.filters {
    width: 100%;
    height: 100%;
    display: grid;
    grid-auto-flow: row;
    grid-auto-rows: 4em;
    background-color: #ebe6e6;
    z-index: 200;
}
.filters .search {
    width: 90%;
    margin-right: auto;
    margin-left: auto;
    grid-template-columns: 10%;
    background-color: white;
}
.filters input[type='checkbox']{
    width: 25px;
    margin: auto;
}
.filters .search .icon {
    width: 100%;
    height: 1.2em;
    margin: auto;
}
.filters .search input {
    margin: auto;
}
.filters .subFilters {
    width: 90%;
    display: grid;
    grid-auto-flow: row;
    grid-auto-rows: 3.5em;
    margin-left: auto;
    margin-right: auto;
}
.filters .subFilters label {
    width: 100%;
    height: 3em;
    line-height: 3em;
    background-color: rgb(221, 219, 219);
}
.filters span {
    display: grid;
    line-height: 3em;
    grid-auto-flow: column;
}

/* image upload component */
.imgUpload {
    width: 90%;
    height: 15em;
    background-color: #d1d1d1;
    color: white;
    text-align: center;
    line-height: 13em;
}
.imgUpload img {
    width: 100%;
    height: inherit;
    margin:auto;
    object-fit: contain;
}
.imgUpload input {
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

/* add product form */
.addProductForm {
    width: 90%;
    margin-left: auto;
    margin-right: auto;
    display: grid;
    row-gap: 2%;
    grid-auto-flow: row;
    text-align: left;
    padding-bottom: 5%;
}

.addProductForm button {
    margin-right: 10%;
}
.addProductForm div {
    width: 90%;
    margin-right: auto;
    display: grid;
    grid-auto-flow: row;
    flex-wrap: wrap;
}
.addProductForm div label {
    width: 90%;
    height: 3em;
    line-height: 3em;
    flex-basis: 40%;
}
.addProductForm .input1, select {
    margin-right: 10%;
}

/* dashboard navigation bar */
.dashNav {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

/* category banner styling */
.categoryBanner {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    display: grid;
    grid-auto-flow: row;
}
.categoryBanner button {
    width: 15em;
    height: 3em;
    line-height: 3em;
    margin-left: auto;
    margin-right: auto;
    color: white;
    background-color: #202020;
}
/* category icons */
.categoryIcons {
    color: #F62147;
    margin: 1%;
    min-height: calc(max-content);
    display: grid;
    line-height:  20px;
    text-align: center;
    border-radius: 15px;
    box-shadow: 0px 0px 18px rgba(0, 0, 0, 0.075);
    background-color: #ffffff;
    transition: height 0.7s, width 0.7s;
    -webkit-transition: height 0.7s, width 0.7s;
    -o-transition: height 0.7s, width 0.7s;
}

.categoryIcons p {
    width: 100%;
    font-size: 20px;
    font-size: 14px;
    font-weight: light;
}
.categoryIcons img{
    width: 80px;
    height: 80px;
    margin: auto;
    scale: 1.1;
    object-fit: contain;
}

/* auth form styling */
.authForm {
    width: 40%;
    min-height: 30vh;
    height: 80%;
    padding: 1%;
    display: grid;
    grid-auto-flow: row;
    box-shadow: 0px 8px 16px rgba(218, 71, 71, 0.137);
}
.authForm * {
    margin-top: 2%;
    width: 90%;
}
/* spinner styling */
.spinner {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-top: calc((100% - 40px)/3);
    margin-left: calc((100% - 40px)/2);
    position: absolute;
    z-index: 500;
    background-color: transparent;
    border: 4px solid #ffbdc9;
    border-top: 4px solid #F62147;
    animation: spin 0.5s forwards linear infinite ;
}
@keyframes spin {
    from{transform: rotateZ(-360deg);}
}

/* category listing styling */
.category {
    width: 90%;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    transition: height 2s;
}
/* show product */
.showProduct {
    width: 15vw;
    height: max-content;
    margin: 1%;
    display: grid;
    grid-auto-flow: row;
    padding: 2%;
    min-width: 15em;
    box-shadow: 0px 0px 16px rgba(0, 0, 0, 0.151);
}
.showProduct .tags {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    gap: 0.5%;
}
.showProduct .description h2 {
    color: #F62147;
    font-size: 18px;
    font-weight: light;
}
.showProduct img {
    width: 80%;
    height: 20vh;
    margin-left: auto;
    margin-right: auto;
    object-fit: contain;
}

/* top navigation */
.homeTopNav {
    width: 100%;
    height: 300px;
    background-color: #F62147;
}
.homeTopNav .banner {
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: #F62147;
    top: 0;
    left: 0;
}
.homeTopNav .banner h1 {
    font-size: 5vw;
    color: white;
    width: 70%;
    margin-top: auto;
    margin-left: auto;
}
.homeTopNav div img {
    height: inherit !important;
    object-fit: cover;
    position: absolute;
    left: 0;
    top: 0;
}
/* navigation bar */
.mainNav {
    width: 100% !important;
    height: 5em !important;
    display: grid;
    grid-auto-flow: row;
    grid-template-rows: 70%;
}
.mainNav div {
    display: grid;
    grid-auto-flow: column;
    justify-content:space-evenly;
    grid-auto-columns: max-content;
}
.mainNav .linkBtns {
    margin-top: auto;
    margin-bottom: auto;
    margin-left: auto;
    width: 90%;
    display: grid;
    grid-auto-flow: column;
    justify-content: flex-end;
    gap: 2%;
}
.mobileSearchBar {
    width: 100%;
    height: 3em;
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: 70%;
    background-color: white;
    box-shadow: 0px 4px 14px rgba(0, 0, 0, 0.26);
}
.searchBar{
    width: 90%;
    height: 2em;
    line-height: 2em;
    margin-top: auto;
    margin-bottom: auto;
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: 80% 20%;
    background-color: gray;
}
.searchBar input {
    height: 30px;
    margin-left: 0;
}
.searchBar .filterBtn{
    height: 100%;
    background-color: gray;
    color: white;
}
.searchBar .dropDown {
    position: absolute;
    top: 120%;
    margin-left: 0;
    right: 0;
    width: 30vh;
    display: grid;
    background-color: white;
    padding-bottom: 2%;
}
